import { BrowserRouter, Route, Routes } from 'react-router-dom';
import './App.css';
import Header from './components/Header/Header';
import Footer from './components/Footer/Footer';
import Home from './Pages/Home/Home';
import Quiz from './Pages/Quiz/Quiz'
import Result from './Pages/Result/Result'
import React, { useState } from 'react';
import axios from 'axios';

// Import the functions you need from the SDKs you need
import { initializeApp } from "firebase/app";
import { getAnalytics } from "firebase/analytics";
// TODO: Add SDKs for Firebase products that you want to use
// https://firebase.google.com/docs/web/setup#available-libraries

// Your web app's Firebase configuration
// For Firebase JS SDK v7.20.0 and later, measurementId is optional
const firebaseConfig = {
  apiKey: "AIzaSyAhRLyRD_sBRU34P6nRr_cUEEDwpUcJ4uw",
  authDomain: "quizzapp-9acec.firebaseapp.com",
  projectId: "quizzapp-9acec",
  storageBucket: "quizzapp-9acec.appspot.com",
  messagingSenderId: "1017322852184",
  appId: "1:1017322852184:web:28cb4e5b2cf18128099f2d",
  measurementId: "G-VPR4R5GVHV"
};

// Initialize Firebase
const app = initializeApp(firebaseConfig);
const analytics = getAnalytics(app);

function App() {
  const [name, setName] = useState('')
  const [questions, setQuestion] = useState()
  const [score, setScore] = useState(0)

  const fetchQuestions = async (category = '', difficulty = '') => {
    const { data } = await axios.get(
      `https://opentdb.com/api.php?amount=10&category=${category}&difficulty=${difficulty}&type=multiple`
    );
      setQuestion(data.results)
    }

  return (
    <BrowserRouter>
      <div className="container">
        <Header/>

        <Routes>
          <Route path='/' element={<Home name={name} setName={setName} fetchQuestions={fetchQuestions}/>}>
          </Route> 

          <Route path='/quiz' element={<Quiz
            name={name}
            questions={questions}
            score={score}
            setScore={setScore}
            setQuestion={setQuestion}
          />}>
          </Route>

          <Route path='/result' element={<Result name={name} score={score}/>}>
          </Route>
        </Routes>

      </div>
      <Footer/>
    </BrowserRouter>
  );
}

export default App;
